html {
  background-color: rgb(240, 240, 240);
}

body {
  font-family: "Rubik", sans-serif;
}

.app {
  padding: 20px;
  margin: 0 auto;
  width: 100vw;
  height: 90vh;
}

.navbar {
  background-color:  rgb(240, 240, 240);
  -webkit-box-shadow: 0 2px 6px -6px black;
	-moz-box-shadow: 0 2px 6px -6px black;
	box-shadow: 0 2px 6px -6px black;
}

#navbar-link {
  color: black;
}

#tool-title {
  font-weight: 600;
  color: rgb(0, 0, 0);
  font-size: 20px;
  transition: 0.3s;
}

#tiles {
  position: relative;
}

.panel-block {
  height: 100%;
}

#panel-tile, .mapviewer, #graph-container {
  background-color: rgba(255, 255, 255, 0);
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.21);
}

.panel-block, .panel-tabs, .panel-block, .countries-list {
  background-color: white;
}

.countries-list {
  border-radius: 0px 0px 5px 5px;
  height: 100%;
}

#refresh-button,
#more-dropdown,
#home-button {
  color: white;
  transition: 0.3s;
}

#contact-button {
  transition: 0.3s;
}

#refresh-button:hover,
#more-dropdown:hover,
#home-button:hover,
#tool-title:hover {
  color: rgb(247, 186, 56);
}

#github-icon {
  float: right;
  margin-top: 5px;
  margin-right: 10px;
  color: white;
  transition: 0.3s;
}

#github-icon:hover {
  color: rgb(240, 211, 46);
}

#search-tool {
  width: 30%;
}

.countries-list {
  height: 146vh;
  padding: 0;
  margin: 0;
  overflow: scroll;
  overflow-x: hidden;
}

.bg-overlay-is-active {
  width: 100vw;
  height: 100vh;
  background-color: black;
  opacity: 0.8;
}

.leaflet-container {
  width: 100%;
  height: 70vh;
  margin: auto;
}

.country-names {
  color: black;
}

.cases {
  color: black;
}

.mapviewer {
  z-index: 0;
}

.panel {
  z-index: 0;
}

.flag-image {
  padding-left: 30%;
  padding-bottom: 30px;
}

.modal-card-title {
  text-align: center;
}

.pulse:hover {
  fill: rgb(173, 125, 53);
  fill-opacity: 0.4;
  transition: all 0.3s;
}

#graph-container {
  width: 100%;
}

#graph-block {
  width: 100%;
  height: 100%;
}

#main-footer {
  margin: 1em;
}

@media (max-width: 1057px) {
  #search-tool {
    width: 100%;
  }
}